<template>
  <div class="IocnTable shadownItemBox">
    <div class="title">当前行情</div>
    <div class="table"  v-loading="load">
      <table>
        <thead>
          <tr>
            <th class="textLeft">交易对</th>
            <th class="textCenter">
              最新价
              <i class="iconSort" @click="sortList('price')"></i>
            </th>
            <th class="textCenter">
              24H涨跌
              <i class="iconSort" @click="sortList('dayRate')"></i>
            </th>
            <!-- <th class="textCenter">24H高 / 低 <i class="iconSort"></i></th> -->
            <th class="textRight">
              24H成交量
              <i class="iconSort" @click="sortList('dayDeal')"></i>
            </th>
            <th class="textRight" width="150">操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,index) in coinList" :key="index">
            <td>
              <i class="iconImg" :class="item.coinName"></i>
              <b>{{item.coinName}}</b>
              <span class="them_color_gray_light">/ USDT</span>
            </td>
            <td class="textCenter">{{Number(item.price).toFixed(2)}}</td>
            <td class="textCenter">
              <b :class="item.dayRate < 0 ?'color-red':'color-green'">
                {{Number(item.dayRate).toFixed(2)}} %
                <i :class="item.dayRate < 0 ?'color-red el-icon-bottom':'color-green el-icon-top'"></i>
              </b>
            </td>
            <!-- <td class="textCenter">123123.545 / 123123.554</td> -->
            <td class="textRight">{{Number(item.dayDeal).toFixed(2)}} USDT</td>
            <td class="textRight">
              <i class="action hoverTr3" @click="gopage('/exchange')" title="立即兑换"></i>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- table -->
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import { getTickerList } from "../../data/apis";
export default {
  props: ["dataGroup"],
  data() {
    return {
      coinList: [],
      load:false,
      sortType:"ASC" //降序 ASC
    };
  },
  methods:{
    sortList(name){//排序字段
      this.sortType = this.sortType=='ASC'?'DESC':'ASC';
      this.coinList.sort((a,b)=>{
        return this.sortType == 'ASC'? (a[name] - b[name] ) : (b[name] - a[name]);
      })
    },//sortList
    getData(){
      this.load = true;
      getTickerList().then(v => {
        this.coinList = v.data
        this.load = false;
      });
    }//getData
  },
  mounted() {
    this.getData();
  }
};
</script>
<style rel="stylesheet/scss" scoped lang="scss">
@import "../../styles/Val";

.IocnTable {
  margin-top: 50px;
  th{
    width: 20%;
  }
}
</style>



