<template>
  <div class="IocnTable shadownItemBox">

    <div class="title">持币排行榜</div>

    <div class="table" v-loading="load">
      <table>
        <thead>

          <tr>
            <th class="textCenter" width="100">排名</th>
            <th class="textCenter" width="200">用户</th>
            <th class="textCenter">持有量 (WMC)</th>
            <th class="textCenter">市值 (USDT)</th>
            <th class="textCenter">占比</th>
          </tr>

        </thead>

        <tbody v-if="sysInfo">

          <tr v-for="(item,index) in sysInfo.rank" :key="index">
            <td class="them_color textCenter"><b>{{++index}}</b></td>
            <td class="textCenter"><span>{{item.userName}}</span></td>
            <td class="textCenter">{{item.amount}}</td>
            <td class="textCenter">{{(Number(item.amount) * Number(item.price)).toFixed(4)}}</td>
            <td class="textCenter">{{toFixed_num(item.rate,2)}}%</td>
          </tr>

        </tbody>

      </table>

    </div>
    <!-- table -->

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState
  } from 'vuex'

  import headImg from '../../assets/img/userIocn.png'

  export default {
     props: ['sysInfo','load'],
    data() {
      return {
        headImg
      }
    },
    components: {

    },
    computed: {

    },
    methods: {

    },
    mounted() {

    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .IocnTable {
    margin-top: 50px;
  }
  .userIcon{
    background: rgba(0,0,0,0.04);
  }

</style>



