<template>
  <div class="notes" v-if="list.length">

    <div class="flex align between">
      <div class="text ellipse" @click="gopage(`/helpDetail?id=${item.id}`)" v-for="(item,index) in list" :key="index">{{item.title}}</div>
    </div>

  </div>
</template>

<script>
  import {
    noticeNews
  } from '../../data/apis';
  export default {
    data() {
      return {
        list: [],
        query: {
          pageSize: 4,
          pageNum: 1,
          type: 'notice'
        }
      }
    },
    methods: {
      getData() {
        noticeNews(this.query).then(v => {
          this.list = v.data.list;
        })
      } //getData
    },
    mounted() {
      this.getData();
    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .notes {
    background: white;
    padding: 20px 60px;
    .text{
      padding: 0 20px;
      width: 25%;
      position: relative;
      font-size: 14px;
      text-align: center;
      cursor: pointer;
      &:hover{
        color: $them_color;
      }
      &:nth-of-type(1)::after{
        display: none;
      }
    }
    .text::after {
      content: "/";
      display: inline-block;
      position: absolute;
      left: 0;
      z-index: 10;
      color: rgba(31, 35, 51, .5);
    }

  }

</style>



