<template>
  <div class="banner" v-loading="load">


    <div class="block">
      <el-carousel trigger="click" height="600px">
        <el-carousel-item v-for="(item,index) in list" :key="index">
        <img :src="setImgUrl(item.img)" alt="" @click="openLink(item.bcontent)">
        </el-carousel-item>
      </el-carousel>
    </div>

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState
  } from 'vuex'

  import { getBanner } from "../../data/apis";

  export default {
    data() {
      return {
        list:[],
        load:false
      }
    },
    methods: {
      getData(){//获取banner
        this.load = true;
        getBanner().then(v=>{
          console.log(v)
          this.list = v.data.list;
          this.load = false;
        })
      }//getData
    },
    mounted() {
      this.getData();
    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .banner {
    min-height: 250px;
    background: rgba(0,0,0,0.05);
    img{
      width: 100%;
      height: 100%;
      display: block;
      cursor: pointer;
    }
  }

</style>



