<template>
  <div id="home">
      <Nav page="home" className="homeTop"/>
      <Banner/>
      <UserIncome/>
      <Notes/>
      <DataView :sysInfo="sysInfo" :load="infoLoad"/>
      <CoinTable/>
      <UserTable :sysInfo="sysInfo" :load="infoLoad"/>
      <BottomInfo/>
      <Footer/>
  </div>
  <!-- index -->
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import Nav from './nav'
  import Banner from '@com/home/banner'
  import DataView from '@com/home/dataView'
  import CoinTable from '@com/home/coinTable'
  import UserTable from '@com/home/userTable'
  import BottomInfo from '@com/home/bottomInfo'
  import Notes from '@com/home/notes'
  import UserIncome from '@com/home/userIncome'
  import Footer from './footer'
  

  import { systemInfo } from "../data/apis";
  
  
  export default {
    data() {
      return {
        sysInfo:null,//概况信息
        infoLoad:true//概况与排行加载状态
      }
    },
    components: {
      Nav,Banner,DataView,CoinTable,UserTable,BottomInfo,Footer,Notes,UserIncome
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      ...mapMutations(['setUserInfo']),
      ...mapActions(['actionUserInfo']),
      getSysInfo(){//获取参数
        this.infoLoad = true;
        systemInfo().then(v=>{
          console.log('----',v);
          this.sysInfo = v.data;
          this.infoLoad = false;
        })
      }//getData
    },
    mounted() {
      this.getSysInfo();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";
  
</style>



