<template>
  <div class="UserIncome">
    <div class="space30"></div>
    <div class="DataView shadownItemBox flex align between">
      <div class="box">
        <div class="title">实时收益 / WMC</div>
        <div v-if="userInfo">
          <div class="date flex align">预计
            <van-count-down
              @finish="finishEvent"
              class="animated fadeIn"
              :time="endTime"
              v-if="endTime"
            />
            <span v-else>&nbsp;--&nbsp;</span>后收益到账
          </div>
          <div class="num animated fadeIn" v-if="incomeNum">
            {{incomeNum[0]}}
            <small>.{{incomeNum[1]}}</small>
          </div>
          <div class="num" v-else>--</div>
        </div>
        <div class="p" v-else>您还没有登录账号哦~
          <br>
          <a @click="gopage('/login')">立即登录
            <i class="el-icon-right"></i>
          </a>
        </div>
      </div>
      <img src="../../assets/img/Currecny.png" class="icon">
    </div>
    <!-- DataView -->
  </div>
</template>

<script>
import Vue from "vue";
import { CountDown } from "vant";

Vue.use(CountDown);
import { mapMutations, mapState } from "vuex";
import { shareIndexIncome } from "../../data/apis";

let timer; //定时器

export default {
  data() {
    return {
      info: null,
      incomeNum: null //收益数量
    };
  },
  computed: {
    ...mapState(["userInfo"]),
    endTime() {
      //到账时间
      if (this.info) {
        // let last = (Number(this.info.endTime - this.info.current) - Number(this.info.startTime  -  this.info.current));
        let last = this.info.endTime - this.info.current
        // let t =  last - this.info.current;
        console.log('剩余时间:',last)
        return last;
      } else {
        return 0;
      }
    } //endTime
  },
  methods: {
    getData() {
      shareIndexIncome().then(v => {
        /*测试数据

          v.data = {
            startTime:'1595166061000',//2020-07-19 21:41:01
            endTime:'1595425261000',//2020-07-22 21:41:01
            number:300,
            current:'1595338861000' //2020-07-21 21:41:01
          }
          */

        this.info = v.data;
        this.getIncome();
      });
    }, //getData
    getIncome() {
      //收益
      let sub_s =
        (Number(this.info.endTime) - Number(this.info.startTime)) / 1000; //获取时间差，秒单位
      let sPrice = Number(this.info.number) / sub_s; //每秒产生少收益
      // 计算已经过了的秒数
      let last = (Number(this.info.current) - Number(this.info.startTime)) / 1000;
      console.log('已过秒数:',last)
      let getNum =
        last *
        sPrice; //已经产生的收益
      timer = setInterval(() => {
        console.log("setInterval doing");
        getNum += sPrice;
        let res = this.toFixed_num(getNum, 8);
        this.incomeNum = res.split(".");
      }, 1000);
    }, //getIncome
    finishEvent() {
      //倒计时结束
      console.log("countdown over");
      clearInterval(timer); //清除定时器
      this.info = null; //清空数据
      this.getData();
    } //finishEvent
  },
  beforeDestroy() {
    console.log("clear clearInterval");
    clearInterval(timer); //清除定时器
  },
  mounted() {
    clearInterval(timer); //清除定时器
    if (this.userInfo) {
      this.getData();
    }
  }
};
</script>
<style rel="stylesheet/scss" scoped lang="scss">
@import "../../styles/Val";

.UserIncome {
  .num {
    font-size: 60px;
    color: $them_color;
    font-weight: bold;
    text-shadow: 0px 0px 5px rgba(127, 163, 212, 0.8);
    letter-spacing: 2px;
    margin-top: 10px;
    small {
      font-size: 40px;
    }
  }
  .date {
    color: $them_color_gray;
    font-size: 16px;
  }
  .p {
    color: $them_color_gray;
    font-size: 18px;
    line-height: 2em;
    a {
      color: $them_color;
      cursor: pointer;
    }
  }

  .icon {
    display: block;
    width: 150px;
    margin-right: 40px;
  }
}
</style>



